<template>
	<div id="msg" class="warp">
		<!--头部按钮组 start-->
		<div class="btn-group">
			<button type="button" class="add" @click="readys">标记为已读</button>
			<button type="button" class="del" @click="multipleDeletePrinter">删除</button>
		</div>
		<!--头部按钮组 end-->

		<!--表格列表 start-->
		<div class="msglist">
			<el-table  stripe :data='msgList' height="680" @selection-change="handleSelectionChange" @sort-change="sort">
				<el-table-column type="selection" min-width="52">
				</el-table-column>
				<el-table-column label="标题" align="center" min-width="650">
					<template slot-scope="scope">
						<router-link :to="{path:'/message/info',query:{newsID:scope.row.news_id}}">
							<span :class="{'bd':scope.row.is_ready===Markread.NO}">{{ scope.row.title }}</span>
						</router-link>
					</template>
				</el-table-column>
				<el-table-column label="发送人" align="center" min-width="300">
					<template slot-scope="scope">
						<span>{{scope.row.send_username }}</span>
					</template>
				</el-table-column>
				<el-table-column sortable="true" label="发送时间" align="center" min-width="330" prop="sort_ctime">
					<template slot-scope="scope">
						<span>{{scope.row.ctime}}</span>
					</template>
				</el-table-column>
			</el-table>

      <empty-table v-if="isListEmpty"></empty-table>

			<!-- 分页 starat -->
			<div class="pagination-container" ref='page'>
				<el-pagination @size-change="handleSizeChange" 
          @current-change="handleCurrentChange" 
          :current-page.sync="listQuery.page" 
          :page-sizes="[10,20,40]" 
          :page-size="listQuery.limit" 
          layout="sizes, jumper, prev, pager, next" :total='total'>
				</el-pagination>
			</div>
			<!-- 分页 end -->
		</div>
		<!--表格列表 end-->
		<!--保存成功失败 Tips start-->
		<div class="tips" v-if='showTips'>
			<span class="info">{{msg}}</span>
		</div>
		<!--保存成功失败 Tips end-->
	</div>
</template>

<script>
import moment from "moment";
import EmptyTable from "@/components/emptytable/main"; //空列表

import { getSysmsg, Delmsg, Savemsg } from "../api";
import { Comethods } from "../index";
import { Markread } from "@/config/cfg";
import { ls } from "@/config/pageStore";
export default {
  components: {
    EmptyTable
  },
  data() {
    return {
      msgList: [], //保存请求回来的消息数据
      testList: [], //用于分页list过滤
      total: 40, //总条目数
      listQuery: {
        page: 1,
        limit: 10,
        sort: "+id"
      },
      multipleSelection: [], //存放多选选中数据
      selectIndex: {}, //记录某页的勾选项
      showTips: false, //控制显示Tips提示框
      msg: "", //提示内容

      //排序
      isSort: false,
      sortLabel: "",
      sortOrder: 1,

      isListEmpty: false
    };
  },
  created() {
    this.initTableData();
  },
  computed: {
    Markread() {
      return Markread;
    }
  },
  methods: {
    //初始化表格信息
    initTableData() {
      //获取每页选择显示的条目数的历史记录
      if (ls.getItem("itemNum") != null) {
        this.listQuery.limit = Number(ls.getItem("itemNum"));
      }
      //获取每页选择页面的历史记录
      if (sessionStorage.getItem("pageNum") != null) {
        this.listQuery.page = Number(sessionStorage.getItem("pageNum"));
      }

      let data = {
        sysnews_list: 1
      };
      //是否有排序
      if (this.isSort) {
        data[this.sortLabel] = this.sortOrder;
      }
      getSysmsg(data, this.getListHandler);
      getSysmsg(data, resp => {
        if (resp.ret === 0) {
          this.msgList = resp.data.list || [];

          if (this.msgList.length === 0) {
            this.isListEmpty = true;
          } else {
            this.isListEmpty = false;
          }

          this.msgList.map(item => {
            if (item.ctime) {
              item["ctime"] = moment(item.ctime * 1000).format(
                "YYYY-MM-DD HH:mm:ss"
              );
              return item;
            }
          });

          this.testList = resp.data.list || [];
          this.total = Comethods.getTotal(this.testList);
          this.msgList = Comethods.getPageList(
            this.testList,
            this.listQuery.limit,
            this.listQuery.page
          );
        }
      });
    },

    //分页事件处理，条数和当前页数改变触发
    handleSizeChange(val) {
      this.listQuery.limit = val;
      ls.setItem("itemNum", this.listQuery.limit);
      this.msgList = Comethods.getPageList(
        this.testList,
        this.listQuery.limit,
        this.listQuery.page
      );
      this.total = Comethods.getTotal(this.testList);
    },
    //目标页
    handleCurrentChange(val) {
      this.listQuery.page = val;
      this.total = Comethods.getTotal(this.testList);
      this.msgList = Comethods.getPageList(
        this.testList,
        this.listQuery.limit,
        this.listQuery.page
      );
      let allSelectIndex = this.selectIndex[val];
      let indexArr = [];
      if (allSelectIndex !== undefined) {
        for (let i in this.selectIndex[val]) {
          let index = Number(this.selectIndex[val][i] + 1);
          $(".el-checkbox__input")
            .eq(index)
            .addClass("is-checked");
        }
      }
      sessionStorage.setItem("pageNum", this.listQuery.page);
    },
    //多选
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    //删除消息
    multipleDeletePrinter() {
      if (this.multipleSelection.length === 0) {
        this.showTips = true;
        this.msg = "未选中任何消息";
        setTimeout(() => {
          this.showTips = false;
        }, 1500);
        return;
      }
      let msgArr = this.multipleSelection.map(function(item) {
        return item.id;
      });
      let data = {
        del_sysnews: 1,
        id_list: msgArr
      };
      Delmsg(data, this.opearteHandler);
    },
    //删除成功后的处理
    opearteHandler(resp) {
      if (resp.ret === 0) {
        this.initTableData();
      } else {
        console.warn("操作失败！");
      }
    },
    //点击标记已读
    readys() {
      if (this.multipleSelection.length === 0) {
        this.showTips = true;
        this.msg = "未选中任何消息";
        setTimeout(() => {
          this.showTips = false;
        }, 1500);
        return;
      }
      let readyArr = this.multipleSelection.map(function(item) {
        return item.id;
      });
      let data = {
        ready_news: 1,
        id_list: readyArr
      };

      Savemsg(data, this.alts);
      this.initTableData();
    },
    //点击标记已读成功的回调
    alts(resp) {
      if (resp.ret === 0) {
        this.showTips = true;
        this.msg = "标记成功";
        setTimeout(() => {
          this.showTips = false;
        }, 1500);
      } else {
        this.showTips = true;
        this.msg = "标记失败";
        setTimeout(() => {
          this.showTips = false;
        }, 1500);
      }
    },
    //排序
    sort(item) {
      this.sortLabel = item.prop;

      if (item.order === "descending") {
        this.isSort = true;
        this.sortOrder = -1;
      } else if (item.order === "ascending") {
        this.isSort = true;
        this.sortOrder = 1;
      } else {
        this.isSort = false;
      }

      this.initTableData();
    }
  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";
#msg {
  padding: 0 14px;
}
.warp {
  width: 100%;
  height: 100%;
  background: #fff;
  position: relative;
  .empty {
    position: absolute;
    left: 0;
    top: 120px;
    width: 100%;
    height: 610px;
    background: #fff;
    text-align: center;
    padding-top: 189px;
    span {
      @include fc(14px,#9b9b9b);
    }
  }
  .btn-group {
    padding: 20px 0;
    background: #fff;
    text-align: right;
    button {
      cursor: pointer;
    }
    .add {
      @include wh(100px, 34px);
      @include fc(14px, #4877e7);
      background: #fff;
      border: 1px solid #4877e7;
      margin-right: 9px;
    }
    .del {
      @include wh(100px, 34px);
      @include fc(14px, #E7487E);
      border: 1px solid #e7487e;
      background: #fff;
    }
  }
  .tips {
    position: fixed;
    @include wh(360px, 100px);
    background: #000;
    opacity: 0.8;
    left: 50%;
    top: 50%;
    margin-left: -180px;
    margin-top: -50px;
    text-align: center;
    border-radius: 2px;
    .info {
      display: inline-block;
      @include fc(14px, #fff);
      padding-top: 40px;
      letter-spacing: 0.5;
    }
  }
}
</style>
<style lang="scss">
@import "src/styles/mixin.scss";
#msg {
  .pagination-container {
    text-align: center;
    padding: 50px 14px 20px;
  }
  a {
    color: #333;
  }
  .bd {
    color: #333;
    font-weight: bold;
  }
  // .el-table__empty-block {
  //   display: none;
  // }
  // .el-table__empty-text {
  //   display: none;
  // }
  .el-table td {
    height: 50px;
  }
}
</style>


